import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';  //  Switch  Routes
import App from './App';  // 
import ProjectDetail from './pages/ProjectDetail';  // 

ReactDOM.render(
  <React.StrictMode>
    {/*  basename  "/portfolio" */}
    <Router basename="/portfolio">
      <Switch>
        {/* Home  */}
        <Route path="/" component={App} exact />  {/*  component  element exact  */}

        {/* Project Detail  projectId  */}
        <Route path="/projects/:projectId" component={ProjectDetail} />  {/*  component */}
      </Switch>
    </Router>
  </React.StrictMode>,
  document.getElementById('root')
);
